@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300&family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App {
  font-family: 'Oswald', sans-serif;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Header{background:url(/EPK/static/media/HeaderImg1.6b9669fe.jpg) no-repeat center center fixed;height:100vh;display:flex;justify-content:center;width:100%}.Header .logo{display:flex;width:60%;align-self:center}.Header .logo img{width:100%}

.Main .generalInfo{max-width:990px;margin:0 auto}.Main .generalInfo span{font-size:1.5rem}.Main .generalInfo .links{font-size:1.2rem;display:flex;grid-gap:10px;gap:10px}.Main .generalInfo p{font-family:'Inter', sans-serif;font-size:1.3rem}.Main .mainImg--container{width:100%}.Main .mainImg--container img{width:100%}

.VideoItem{margin-bottom:1em}.VideoItem .video-title{font-family:'Oswald', sans-serif;font-size:1.8rem}.VideoItem .videoContainer{position:relative;width:100%;height:0;padding-bottom:56.25%}.VideoItem .videoContainer .video{position:absolute;top:0;left:0;width:100%;height:100%}

.Releases h3,.Releases h4{font-family:'Oswald', sans-serif;font-size:1.8rem}.Releases h4{font-size:1.5rem}.Releases .releases-list{display:flex;flex-direction:column}.Releases .releases-list .ReleaseItem{display:flex;flex-direction:column;justify-content:center}.Releases .releases-list .ReleaseItem img{align-self:center}.Releases .releases-list .ReleaseItem .release-links{display:flex;justify-content:space-evenly;padding:.6em}.Releases .releases-list .ReleaseItem .release-links a{font-family:'Oswald', sans-serif;color:#000;display:block}

